import Vue from 'vue'
import VueRouter from 'vue-router'

const CreateArticle = () => import('./views/CreateArticle');


Vue.use(VueRouter);

const routes = [
  {path: '/', redirect: '/'},
  {path: '/article/create', component: CreateArticle},

];

const router = new VueRouter({
  mode: "hash",
  routes
});

